@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Regular.woff2), url(../fonts/Roboto-Regular.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Bold.woff2), url(../fonts/Roboto-Bold.woff2);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,
ol,
dl {
  list-style: none;
}
.page__body {
  max-width: 100%;
  min-width: 320px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #3B362D;
  background-color: #f6f3f0;
  overflow-x: hidden;
}
.page__caption {
  margin-top: 40px;
  margin-bottom: 25px;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}
@media (min-width: 744px) {
  .page__caption {
    margin-top: 60px;
    margin-bottom: 30px;
  }
}
@media (min-width: 1400px) {
  .page__caption {
    margin-top: 100px;
    margin-bottom: 40px;
    font-size: 36px;
    line-height: 42px;
  }
}
@media (min-width: 1920px) {
  .page__caption {
    margin-top: 60px;
    margin-bottom: 50px;
    font-size: 48px;
    line-height: 56px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1740px;
  margin: 0 auto;
  padding: 0 17px;
}
@media (min-width: 744px) {
  .container {
    padding: 0 61px;
  }
}
@media (min-width: 1400px) {
  .container {
    padding: 0 117px;
  }
}
@media (min-width: 1920px) {
  .container {
    padding: 0;
  }
}
.main__caption {
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
}
.main__caption span {
  color: #6c8679;
}
@media (min-width: 1400px) {
  .main__caption {
    font-size: 36px;
    line-height: 42px;
  }
}
@media (min-width: 1920px) {
  .main__caption {
    font-size: 48px;
    line-height: 56px;
  }
}
.foreword {
  width: 283px;
}
.foreword h2 {
  margin-bottom: 20px;
}
@media (min-width: 744px) {
  .foreword {
    display: flex;
    align-items: center;
    gap: 40px;
    width: 100%;
  }
  .foreword h2 {
    margin: 0;
  }
  .foreword p {
    width: 80%;
  }
}
@media (min-width: 1400px) {
  .foreword {
    gap: 200px;
  }
  .foreword p {
    font-size: 24px;
    line-height: 28px;
  }
}
@media (min-width: 1920px) {
  .foreword {
    gap: 290px;
  }
  .foreword p {
    font-size: 28px;
    line-height: 32px;
  }
}
.primary-button {
  display: block;
  width: 150px;
  padding: 17px 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  background-color: #6c8679;
  border: none;
  border-radius: 58px;
  cursor: pointer;
  box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001;
  transition: all 0.2s ease;
}
.primary-button:hover {
  background-color: #789E96;
}
.primary-button:active {
  opacity: 0.8;
}
.primary-button:disabled {
  background-color: #dcdcdc;
  cursor: default;
}
.primary-button--arrow {
  position: relative;
  padding-right: 35px;
}
.primary-button--arrow::before {
  content: "";
  position: absolute;
  top: calc(50% - 7.5px);
  right: 18px;
  width: 17px;
  height: 15px;
  background: url('../img/icon-arrow-right.svg') no-repeat 50%;
}
.secondary-button {
  display: block;
  min-width: 150px;
  padding: 15px 0;
  font-size: 16px;
  line-height: 18px;
  text-align: center;
  text-decoration: none;
  color: #686656;
  background-color: transparent;
  border: 2px solid #6c8679;
  border-radius: 55px;
  cursor: pointer;
  box-shadow: 0 1px 5px -2px;
  transition: all 0.2s ease;
}
.secondary-button:hover {
  border-color: #789E96;
  box-shadow: 0 4px 10px -2px;
  transform: translateY(-2px);
}
.secondary-button:active {
  border-color: #6c8679;
  opacity: 0.9;
}
.secondary-button:disabled {
  border-color: #dcdcdc;
  cursor: default;
}
.secondary-button--arrow {
  position: relative;
  padding-left: 35px;
}
.secondary-button--arrow::before {
  content: "";
  position: absolute;
  top: calc(50% - 7.5px);
  left: 18px;
  width: 17px;
  height: 15px;
  background: url('../img/icon-arrow-left.svg') no-repeat 50%;
}
.input {
  appearance: none;
  height: 45px;
  padding: 15px;
  font-style: inherit;
  color: #515151;
  border: 3px solid #D5D5D5;
  border-radius: 17px;
}
.input:hover,
.input:focus {
  border-color: #789E96;
}
.input:focus-visible {
  outline: none;
}
@media (min-width: 1400px) {
  .input {
    border-width: 3px;
  }
}
.download-button {
  width: 230px;
  height: 50px;
  margin: 0 auto;
  border-radius: 78px;
}
@media (min-width: 1920px) {
  .download-button {
    height: 60px;
    font-size: 18px;
  }
}
.footer {
  width: 100%;
  box-shadow: 2px 2px 13px 5px rgba(59, 54, 45, 0.6);
  background-color: #6c8679;
}
.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  padding: 30px 17px;
}
@media (min-width: 744px) {
  .footer__wrapper {
    justify-content: space-between;
    flex-wrap: nowrap;
    padding: 30px 60px;
  }
}
@media (min-width: 1400px) {
  .footer__wrapper {
    padding: 45px 120px;
  }
}
@media (min-width: 1920px) {
  .footer__wrapper {
    padding: 90px 0;
  }
}
.footer__logo-container {
  display: flex;
  flex-direction: column;
  max-width: fit-content;
  margin: 0 auto;
}
@media (min-width: 744px) {
  .footer__logo-container {
    margin: 0;
  }
}
@media (min-width: 1920px) {
  .footer__logo-container {
    gap: 15px;
  }
}
.footer__link {
  display: block;
  margin: 0 auto;
  width: 120px;
  height: auto;
}
.footer__link > img {
  max-width: 100%;
}
@media (min-width: 744px) {
  .footer__link {
    margin: 0;
  }
}
.footer__slogan {
  width: 220px;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #E6CC70;
}
@media (min-width: 744px) {
  .footer__slogan {
    text-align: left;
  }
}
@media (min-width: 1400px) {
  .footer__slogan {
    width: 240px;
    font-size: 16px;
    line-height: 18px;
  }
}
@media (min-width: 1920px) {
  .footer__slogan {
    width: 270px;
    font-size: 18px;
    line-height: 21px;
  }
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 25px;
  width: fit-content;
  margin: 0 auto;
}
@media (min-width: 744px) {
  .footer__contacts {
    margin: 0;
  }
}
.footer__phone,
.footer__mail,
.footer__vk {
  position: relative;
  width: fit-content;
  padding-left: 35px;
  font-size: 20px;
  line-height: 23px;
  text-decoration: none;
  color: #E6CC70;
}
.footer__phone:hover,
.footer__mail:hover,
.footer__vk:hover {
  color: #fee17d;
}
.footer__phone:active,
.footer__mail:active,
.footer__vk:active {
  color: #96793d;
}
.footer__phone::before,
.footer__mail::before,
.footer__vk::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 22px;
  background: no-repeat 50% / contain;
}
@media (min-width: 1920px) {
  .footer__phone::before,
  .footer__mail::before,
  .footer__vk::before {
    width: 30px;
    height: 29px;
  }
}
@media (min-width: 1920px) {
  .footer__phone,
  .footer__mail,
  .footer__vk {
    padding-left: 45px;
    font-size: 24px;
    line-height: 28px;
  }
}
.footer__phone::before {
  background-image: url('../img/icon-phone.svg');
}
.footer__mail::before {
  background-image: url('../img/icon-mail.svg');
}
.footer__vk::before {
  background-image: url('../img/icon-vk.svg');
}
.gallery {
  margin-bottom: 60px;
}
@media (min-width: 744px) {
  .gallery {
    margin-bottom: 85px;
  }
}
@media (min-width: 1400px) {
  .gallery {
    margin-bottom: 65px;
  }
}
@media (min-width: 1920px) {
  .gallery {
    margin-bottom: 100px;
  }
}
.gallery__caption {
  margin-bottom: 40px;
}
@media (min-width: 744px) {
  .gallery__caption {
    margin-bottom: 25px;
  }
}
@media (min-width: 1400px) {
  .gallery__caption {
    margin-bottom: 50px;
  }
}
@media (min-width: 1920px) {
  .gallery__caption {
    margin-bottom: 80px;
  }
}
.gallery__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 100px;
  grid-auto-flow: dense;
  gap: 18px 12px;
  margin-bottom: 40px;
}
@media (min-width: 744px) {
  .gallery__list {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 140px;
    gap: 26px 16px;
    margin-bottom: 25px;
  }
}
@media (min-width: 1400px) {
  .gallery__list {
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 130px;
    gap: 25px 15px;
    margin-bottom: 50px;
  }
}
@media (min-width: 1920px) {
  .gallery__list {
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: 190px;
    gap: 35px 25px;
  }
}
.gallery__item {
  border-radius: 7px;
  overflow: hidden;
}
.gallery__item > .gallery__img {
  transition: all 0.5s ease;
}
.gallery__item:hover > .gallery__img {
  transform: scale(1.1);
  transition: all 0.5s ease;
}
.gallery__item:nth-child(odd) {
  grid-row: span 2;
}
@media (min-width: 744px) {
  .gallery__item:nth-child(6n-3) {
    order: 5;
  }
}
@media (min-width: 1400px) {
  .gallery__item:nth-child(6n) {
    order: 5;
  }
}
@media (min-width: 1920px) {
  .gallery__item:nth-child(6n-3) {
    order: 0;
  }
}
.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header {
  position: relative;
  margin-bottom: 40px;
  padding: 16px;
  background-color: #6c8679;
  box-shadow: 2px 2px 13px 5px rgba(59, 54, 45, 0.6);
}
@media (min-width: 744px) {
  .header {
    margin-bottom: 50px;
    padding-top: 36px;
  }
}
@media (min-width: 1400px) {
  .header {
    margin-bottom: 70px;
  }
}
@media (min-width: 1920px) {
  .header {
    margin-bottom: 90px;
  }
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: calc(1400px - 200px)) {
  .header__wrapper {
    justify-content: flex-start;
    gap: 70px;
  }
}
.header__logo {
  width: 80px;
  height: auto;
}
.header__logo img {
  max-width: 100%;
}
@media (min-width: 744px) {
  .header__logo {
    width: 120px;
  }
}
.header__nav {
  display: none;
}
@media (min-width: calc(1400px - 200px)) {
  .header__nav {
    display: initial;
  }
}
.nav__list {
  max-width: 250px;
  margin: 130px 0 0 80px;
}
@media (min-width: calc(1400px - 200px)) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 40px;
    max-width: none;
    margin: 0;
  }
}
.nav__item {
  display: block;
  width: fit-content;
  height: 25px;
  margin-bottom: 40px;
}
@media (min-width: calc(1400px - 200px)) {
  .nav__item {
    width: auto;
    margin: 0;
  }
}
.nav__link {
  display: block;
  max-width: 100%;
  font-size: 20px;
  line-height: 23px;
  text-decoration: none;
  color: #E6CC70;
}
.nav__link:hover {
  color: #fee17d;
}
.nav__link:active {
  opacity: 0.8;
}
@media (min-width: 1920px) {
  .nav__link {
    font-size: 24px;
    line-height: 28px;
  }
}
.header__phone {
  margin-left: auto;
  z-index: 5;
}
.header__button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 28px;
  height: 16px;
  border: none;
  background: none;
  cursor: pointer;
}
.header__button:hover .header__burger {
  background-color: #fee17d;
}
.header__button:active .header__burger {
  opacity: 0.8;
}
@media (min-width: 744px) {
  .header__button {
    width: 49px;
    height: 30px;
  }
}
@media (min-width: calc(1400px - 200px)) {
  .header__button {
    display: none;
  }
}
.header__burger {
  width: 100%;
  height: 2px;
  background-color: #E6CC70;
  transition: all 0.3s ease;
}
.header__burger--third {
  width: 50%;
}
@media (min-width: 744px) {
  .header__burger {
    height: 3px;
  }
}
.header__button--cross .header__burger--first {
  transform: translateY(7px) rotate(45deg);
}
@media (min-width: 744px) {
  .header__button--cross .header__burger--first {
    transform: translateY(14px) rotate(45deg);
  }
}
.header__button--cross .header__burger--second {
  transform: rotate(-45deg);
}
.header__button--cross .header__burger--third {
  width: 50%;
  opacity: 0;
}
.header__phone-link {
  display: none;
}
@media (min-width: calc(1400px - 200px)) {
  .header__phone-link {
    position: relative;
    display: initial;
    padding-left: 43px;
    font-size: 20px;
    text-decoration: none;
    color: #E6CC70;
  }
  .header__phone-link:hover {
    color: #fee17d;
  }
  .header__phone-link:active {
    opacity: 0.8;
  }
  .header__phone-link::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 25px;
    height: 24px;
    background: url('../img/icon-phone.svg') no-repeat center;
  }
}
@media (min-width: 1920px) {
  .header__phone-link {
    font-size: 24px;
    line-height: 28px;
  }
}
.header__popup {
  position: absolute;
  top: 0;
  right: 0;
  width: 320px;
  height: 640px;
  border-left: 3px solid #E6CC70;
  border-bottom: 3px solid #E6CC70;
  border-bottom-left-radius: 38px;
  background-color: #6c8679;
  overflow: hidden;
  z-index: 1;
  transform: translateY(-100%);
  transition: all 0.4s ease-out;
}
.header__popup--active {
  transform: translateY(0);
  transition: all 0.4s ease-in;
}
@media (min-width: calc(1400px - 200px)) {
  .header__popup {
    display: none;
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;
  backdrop-filter: blur(2px);
  z-index: -10;
}
.modal--active {
  visibility: visible;
  opacity: 1;
  z-index: 100;
}
.modal--active .modal__wrapper {
  transform: scale(1);
}
.modal__wrapper {
  position: relative;
  width: 100%;
  max-width: 320px;
  padding: 50px 0;
  background-color: #f6f3f0;
  border: 2px solid #6c8679;
  border-radius: 20px;
  transform: scale(0);
  transition: all 0.3s ease-in;
}
@media (min-width: 744px) {
  .modal__wrapper {
    max-width: 520px;
  }
}
@media (min-width: 1400px) {
  .modal__wrapper {
    max-width: 780px;
    padding: 75px 0;
    border-radius: 30px;
  }
}
@media (min-width: 1920px) {
  .modal__wrapper {
    padding: 75px 0;
  }
}
.modal__caption {
  display: none;
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #3B362D;
}
@media (min-width: 1400px) {
  .modal__caption {
    margin-top: 15px;
    font-size: 24px;
    line-height: 28px;
  }
}
@media (min-width: 1920px) {
  .modal__caption {
    margin: 0;
    margin-bottom: 35px;
    font-size: 32px;
    line-height: 37px;
  }
}
.modal__caption--show {
  display: block;
}
.modal__form {
  width: 240px;
  margin: 0 auto;
}
@media (min-width: 1400px) {
  .modal__form {
    width: 360px;
  }
}
.modal__input {
  width: 100%;
  height: 45px;
  margin-bottom: 8px;
}
@media (min-width: 1400px) {
  .modal__input {
    height: 70px;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 12px;
    font-size: 21px;
    line-height: 25px;
    border-radius: 26px;
  }
}
.modal__button {
  width: 230px;
  height: 40px;
  padding: 12px 0;
  padding-right: 35px;
  margin: 0 auto;
  margin-top: 12px;
}
@media (min-width: 1400px) {
  .modal__button {
    width: 320px;
    height: 60px;
    font-size: 21px;
    line-height: 25px;
    border-radius: 68px;
  }
}
.modal__cross {
  position: absolute;
  top: 20px;
  right: 25px;
  width: 15px;
  height: 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal__cross:hover path {
  stroke: #789E96;
}
.modal__cross:active path {
  stroke: #6c8679;
}
@media (min-width: 1400px) {
  .modal__cross {
    top: 30px;
    right: 35px;
    width: 23px;
    height: 23px;
  }
}
.process {
  position: relative;
}
@media (min-width: 744px) {
  .process::after {
    content: "";
    position: absolute;
    top: 80%;
    left: 0;
    width: 100%;
    height: 455px;
    background: url('../img/bg-forest.svg') no-repeat;
  }
}
@media (min-width: 1400px) {
  .process::after {
    top: 60%;
  }
}
@media (min-width: 1400px) {
  .process::after {
    top: 70%;
  }
}
.process__foreword {
  margin-bottom: 50px;
}
@media (min-width: 744px) {
  .process__foreword {
    margin-bottom: 85px;
  }
}
@media (min-width: 1400px) {
  .process__foreword {
    margin-bottom: 100px;
  }
}
@media (min-width: 1920px) {
  .process__foreword {
    margin-bottom: 120px;
  }
}
.process__wrapper {
  width: 100%;
  height: auto;
  margin-bottom: 60px;
}
@media (min-width: 744px) {
  .process__wrapper {
    margin-bottom: 140px;
  }
}
@media (min-width: 1400px) {
  .process__wrapper {
    margin-bottom: 200px;
  }
}
@media (min-width: 1920px) {
  .process__wrapper {
    margin-bottom: 260px;
  }
}
.process__list {
  margin-bottom: 40px;
}
@media (min-width: 744px) {
  .process__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 46px 67px;
    margin: 0;
  }
}
@media (min-width: 1400px) {
  .process__list {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 125px 83px;
  }
}
@media (min-width: 1920px) {
  .process__list {
    gap: 125px 180px;
  }
}
.process__item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.process__content {
  display: grid;
  grid-template-columns: 28px auto;
  gap: 15px 22px;
  justify-self: center;
  max-width: 400px;
  min-width: 0;
}
@media (min-width: 1400px) {
  .process__content {
    grid-template-columns: 37px auto;
    gap: 17px 25px;
  }
}
@media (min-width: 1920px) {
  .process__content {
    grid-template-columns: 42px auto;
    max-width: 450px;
  }
}
.process__number {
  align-self: start;
